
class Player {
  constructor(name){
    this.name = name;
    this.alive = true
    this.protected = false
  }
}

class Villager extends Player{
  constructor(name){
    super(name);
    this.role = 'villager';
    this.role_jp = '';
    this.side = 0; // 
    this.winning_side = 0;
    this.saw = 0 // 
    this.perceived = '' // 
    this.killable = true; // 
    this.action_sentence = '';
    this.night_action = 'suspect';
    this.random_level = 0; // 
    this.description = ''
  }
}

class Werewolf extends Player{
  constructor(name){
    super(name);
    this.role = 'werewolf';
    this.role_jp = '';
    this.side = 1;
    this.winning_side = 1
    this.saw = 1;
    this.perceived = ''
    this.killable = false;
    this.action_sentence = '';
    this.night_action = 'kill'
    this.random_level = 0;
    this.description = '  '
  }
}

