import React from 'react';
import Sound from 'react-sound';


export default class Timer extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      remaining : this.props.setTimerSeconds()
    };
  }

  countDown() {
      if(this.state.remaining > 0) {
            this.setState((prevState) => ({
                  remaining : prevState.remaining - 1
            }));
       }
  }

  componentDidMount() {
    this.interval = setInterval(() => this.countDown(), 1000);
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  render() {
    if (this.state.remaining > 0) {
      return (
        <div className='widget'>
        <h2 className='widget__message'></h2>
          <p className='widget__message'><span className='timer'>{Math.floor(this.state.remaining / 60)}{this.state.remaining % 60}</span></p>
        </div>
      );
    } else {
      return (
        <div>
          {this.props.bgm && <Sound
                url="sounds/fate1.mp3"
                autoLoad={true}
                volume={50}
                playStatus={Sound.status.PLAYING}
              />}
          <div className='widget'>
            <p className='widget__message'> </p>
          </div>
        </div>
      );
    }
  }
}


