import React from 'react';
import Sound from 'react-sound';

const ResultOfNight = (props) => {
  let x = props.handleKilledAtNight(props.turn);
  props.removeProtection(); // x
  let z = props.handleSamuraiKilledAtNight(props.turn);
  if (z !== -1) {
    z = [...new Set(z)]
  }

  let y = props.handleWinningSide();

  let suspected_players = props.mostSuspiciousPlayer(x, z);
  let baker_alive = props.isBakerAlive();

  let victim = '';
  if (x === -1 && z === -1) {
    victim = ''
  } else if (x === -1){
    let name;
    // shuffle
    for (let i = z.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [z[i], z[j]] = [z[j], z[i]];
    }

    for (name of z) {
      victim += ' ' + name + ''
      }

  } else if (z === -1){
    victim = x + ''
  } else {
    if (!z.includes(x)){
      z.push(x);
    }

    // shuffle
    for (let i = z.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [z[i], z[j]] = [z[j], z[i]];
    }

    let name;
    for (name of z) {
      if (x !== z) {
        victim += ' ' + name + ''
      }
    }
  }

  return(
    <div className='widget'>
      {props.bgm && <Sound
                      url="sounds/spring-mountain1.mp3"
                      autoLoad={true}
                      volume={30}
                      playStatus={Sound.status.PLAYING}
                    />}
      <div className='widget widget__message'>
        <p className='option option__text'></p>
        {baker_alive && <p className='option option__text'></p>}
        <p className='option option__text'>{props.turn === 1? "" : '' + victim + ''}</p>
        <p className='option option__text'></p>
        {suspected_players.map((player) => {
          return <p className='option option__text bf' key={player}>{player}</p>;
        })}
        <p className='option option__text'>{!!suspected_players.length ?  '': '' }</p>
      </div>

      <div className="button-wrapper">
        <button
          className='button-single'
          onClick={y < 0 ? props.morningPhase: props.gameResultPhase}
        >
          
        </button>
      </div>
    </div>
    )

}

export default ResultOfNight;
